.header__link {
  color: #a9a9a9;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  text-decoration: none;
}

.header__link:hover {
  opacity: 0.6;
}

@media screen and (max-width: 937px) {
  .header__link {
    font-size: 14px;
    line-height: 16px;
  }
}
